:root {
    --primary-color: #f8fff8;
    --padding-size: 1rem;
    --font-family: "Arial, sans-serif";
  }
*{
    list-style: none;
    margin: 5px;
}
ul li{
    display: block;
    margin: 5px;
}
body{
    margin: 10px;
}
.myProject{
    margin: 20px;
    background-color: var(--primary-color);
    padding: 10px;
    height: 700PX;
    box-sizing: border-box;
    box-shadow: 0px -1px 0px 2px rgba(0, 0, 0, 0.1);
}
h1{
    font-size: xx-large;
    font-weight: bolder;
}
.btnMain{
    width:25%;
    margin-left: 60%;
}
button {
    box-sizing: 1px rgb(0,0,0, 0.5);
    padding: 2px 5px;
    border-radius: 10px;
    display: block;
    margin-top: 5px;
}
button:hover{
    background-color: #c4d6c4;
}
.travelInfo{
    background-color: #c4d6c4;
    padding: 5px;
    margin: 10px;
}
.city, #depDate{
    font-size: xx-large;
    font-weight: bold;
}
.svTrip{
    background-color: #f8fff8;
    box-sizing: border-box ;
    border-radius: 0px;
    width: 50%;
}
.rmvTrip{
    background-color: #c4d6c4;
    box-sizing: border-box ;
    border-color: #f8fff8;
    border-radius: 0px;
    width: 50%;
}
.hidden {
    display: none;
    // background-image: none;
    visibility: hidden;
}
/* Background Image Container */
#backgroundContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('https://images.unsplash.com/photo-1507525428034-b723cf961d3e?ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    z-index: 1; /* Lower z-index */
}

/* Add Trip Button */
.btnMain {
    position: relative; /* Ensure it respects z-index */
    z-index: 2; /* Higher z-index than the background container */
}
.background-content {
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background for better text contrast */
    padding: 20px;
    border-radius: 10px;
}

